function magic_mapNoteTimeToColor(time) {
  // let rgb = [0, 0, 0];
  var COLOR1 = [151, 255, 241];
  var COLOR2 = [98, 166, 223];
  var COLOR3 = [154, 124, 255];
  var TIME_MAX = 20;
  var rgb = [0, 0, 0];
  if (time < 0 || time > TIME_MAX) {
    // console.warn('we don\'t allow time above 20. from \'mapNoteTimeToColor\' util function');
  } else if (time <= 10) {
    rgb = [COLOR1[0] + time * (COLOR2[0] - COLOR1[0]) / (TIME_MAX / 2), COLOR1[1] + time * (COLOR2[1] - COLOR1[1]) / (TIME_MAX / 2), COLOR1[2] + time * (COLOR2[2] - COLOR1[2]) / (TIME_MAX / 2)];
  }
  rgb = [COLOR2[0] + time * (COLOR3[0] - COLOR2[0]) / (TIME_MAX / 2), COLOR2[1] + time * (COLOR3[1] - COLOR2[1]) / (TIME_MAX / 2), COLOR2[2] + time * (COLOR3[2] - COLOR2[2]) / (TIME_MAX / 2)];
  // console.log(`rgb(${Math.floor(rgb[0])},${Math.floor(rgb[1])},${Math.floor(rgb[2])})`);
  return 'rgb(' + Math.floor(rgb[0]) + ',' + Math.floor(rgb[1]) + ',' + Math.floor(rgb[2]) + ')';
}
function magic_mapNoteMidiToLength(noteName) {
  // in our context, the midi range would be from 60 - 84
  // 75 is rem to px
  // return `${(75 * 0.8 * ((Tone.Frequency(noteName).toMidi() - 60) / (84 - 40))) + 0.2}`;
  // 50 - 110
  return '' + (75 * 0.8 * ((Tone.Frequency(noteName).toMidi() - 50) / (110 - 40)) + 0.2); // 75 is rem to px
}

